import SelectInput from "./Select.component";
import useServiceContextHook from "../../../../_hooks/service.context.hook";
import { useEffect, useState } from "react";
/* const currencies = {
  ALL: null,
  USDT: "usdt",
  BTC: "btc",
  EOS: "eos",
  ETH: "eth",
  XRP: "xrp",
};
 */
export default function CryptoCurrency({ onChange, attributes }) {
  const {
    services: { util },
  } = useServiceContextHook();
  const [currencies, setCurrencies] = useState({ "": "All" });

  useEffect(async () => {
    try {
      let { data } = await util.findByName("supported_tokens");
      if (data) setCurrencies((state) => ({ ...state, ...data }));
    } catch (err) {
      console.error(err);
    }
  }, []);
  return (
    <SelectInput
      onChange={onChange}
      attributes={{
        ...{
          name: "crypto-currencies",
          id: "crypto-currencies",
          ...attributes,
        },
      }}
      data={currencies}
    />
  );
}
