/**
 * @description Phone number verification setting component
 * @returns
 */
function PhoneVerificationSetting() {
  const { session } = useServiceContextHook();
  const { t } = useTranslation();
  const { toggledPayload, onOpen, onClose, isOpen } = useToggler();

  function ConfirmRemove() {
    return <>Are you sure?</>;
  }
  function useFormRenderer(data) {
    let { type, formData } = data;
    const decide = function () {
      switch (type) {
        case "remove": {
          return ["Remove Phone Number", <ConfirmRemove />];
        }
        default: {
          return [
            "Phone Number Verification",
            <KycForm.VerifyPhone initialValues={formData} />,
          ];
        }
      }
    };
    return decide();
  }

  return (
    <div
      style={{
        display: "flex",
        gap: 30,
        justifyContent: "space-between",
        alignItems: "center",
      }}
    >
      <div>
        <img src={icon_phone} alt="Phone Number Verification" />
        <dl>
          <dt>{t("Phone Number Verification")}</dt>
          <dd>
            <span>{t("Protect your account and transactions.")}</span>
            <span className="d-block">
              {!session?.user?.profile?.phone && (
                <Badge variant="danger">Phone number not verified yet!</Badge>
              )}
            </span>
          </dd>
        </dl>
      </div>

      {/* Data */}
      <div
        style={{
          display: "flex",
          gap: 10,
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        {session?.user?.profile?.phone && (
          <p>
            <i className="fas fa-check-circle"></i>
            {hideText(session?.user?.profile?.phone)}
          </p>
        )}
        {/* Change button */}
        <div style={{ display: "flex" }}>
          {/* Change/Add button */}
          <button
            style={{ flex: "1" }}
            type="button"
            onClick={() =>
              onOpen({
                type: "change",
                formData: { phone: session?.user?.profile?.phone },
              })
            }
            className="btn btn_change"
          >
            {session?.user?.profile?.phone ? t("Change") : t("Add")}
          </button>
          {/* Remove button */}
          {session?.user?.profile?.phone ? (
            <button
              type="button"
              onClick={() =>
                onOpen({
                  type: "remove",
                  formData: {},
                })
              }
              className="btn btn_remove"
            >
              {t("Remove")}
            </button>
          ) : null}
        </div>
      </div>
      <ModalForm
        {...{ useFormRenderer, isOpen, onClose, formData: toggledPayload }}
      />
    </div>
  );
}

/**
 * @description hides a portion of text and replaces it with a dummy text
 * @param {String} text
 * @param {String} at
 * @returns
 */
function hideText(text = "", at = null, replacement = "*") {
  let [first, ...rest] = String(text)?.split(at || ",");
  let transform =
    String(first)?.substring(0, 3) +
    `${String(replacement).repeat(3)}${at}${rest?.join("")}`;
  return transform;
}

