import { useEffect, useState } from "react";
import { Redirect } from "react-router";
// CONSTANTS
import _constants from "../../../../_constants";
// HOOKS
import useQuery from "../../../../_hooks/query.hook";
import useServiceContextHook from "../../../../_hooks/service.context.hook";
// HELPERS
import { routeMap } from "../../routes";
// COMPONENTS
import AuthForm from "../../components/form/auth.form";

export default function Reset() {
  const { session } = useServiceContextHook();
  const { id, token } = useQuery();
  const [queryID, setQueryID] = useState(id);
  const [queryToken, setQueryToken] = useState(token);

  useEffect(() => {
    if (session?.user) {
      setQueryToken(session?.user?.token);
      setQueryID(session?.user?.id);
    }
  }, [session]);

  return session?.user || (queryID && queryToken) ? (
    <AuthForm.Reset
      id={queryID}
      token={queryToken}
      {...(session?.user && { old_password: true })}
    />
  ) : (
    <Redirect to={{ pathname: routeMap.home }}>No token found</Redirect>
  );
}
