function OfferDetail({ data = null, isOpen, onClose }) {
  const {
    services: { advert, order },
  } = useServiceContextHook();
  const { t } = useTranslation();
  // STATES
  const [timer, setTimer] = useState(30);
  const [advertData, setAdvertData] = useState(data);
  const [isLoading, setIsLoading] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [qty, setQty] = useState(null);
  const [total, setTotal] = useState(null);
  const [errors, setErrors] = useState(null);

  // FUNCTIONS
  function validate() {
    const newErrors = {};
    const { price, min_order_qty, max_order_qty, crypto } = advertData;
    if (qty !== null) {
      if (!isBetween(qty, min_order_qty, max_order_qty)) {
        newErrors.qty = `${String(
          crypto
        )?.toUpperCase()} quantity amount must be between ${min_order_qty} and ${max_order_qty}`;
      }
      setErrors(newErrors);
    }
    return !Object.keys(newErrors)?.length;
  }

  async function refreshAdvert() {
    try {
      setIsLoading(true);
      let response = await advert.findByID(data?.id, {
        fake: true,
        "filter[type]": String(data?.type)?.toUpperCase(),
      });
      if (response?.data) {
        setAdvertData(response?.data);
        setQty({
          fiat: response?.data?.min_order_price,
          crypto: response?.data?.min_order_qty,
        });
        setTimer(30);
      } else if (response.error) {
        throw new Error(
          response.message || "Request error! No data received. Reload page"
        );
      }
    } catch (err) {
      notify(err.message, "error");
    } finally {
      setIsLoading(false);
    }
  }

  function isBetween(value, min, max) {
    return value >= min && value <= max;
  }

  async function onSubmit(e) {
    e.preventDefault();
    if (validate()) {
      try {
        setIsSubmitting(true);
        let { data, error, message } = await order.create({
          advert_id: advertData?.id,
          total_amount: total,
          total_quantity: qty,
        });

        if (!data)
          throw new Error(error?.message || message || `Error placing order`);

        notify("Order has been placed successfully!");
        // TODO: Redirect to the order page
      } catch (err) {
        notify(err.message, "error");
      } finally {
        setIsSubmitting(false);
      }
    }
  }

  function onQtyChange({ target }) {
    const { price } = advertData;
    if (price) {
      setQty(+target.value);
      setTotal(+target.value * price);
    }
  }

  // WATCHERS
  useEffect(() => validate(), [qty]);
  useEffect(() => {
    data && refreshAdvert();
    return advert.abort;
  }, []);

  useEffect(() => {
    if (data && isOpen) {
      const timeout = setInterval(async () => {
        timer ? setTimer(timer - 1) : refreshAdvert();
      }, 1000);
      return () => {
        clearTimeout(timeout);
      };
    }
  }, [isOpen, timer]);

  return (
    <Modal show={isOpen} onHide={onClose} centered size="xl">
      <Modal.Body style={{ padding: 0 }}>
        {isLoading ? (
          <div style={{ padding: 15 }}>
            <Loader />
          </div>
        ) : advertData ? (
          <div style={{ display: "flex", flexWrap: "wrap" }}>
            {/* INFORMATION */}
            <div
              style={{
                flex: "1",
                minWidth: 300,
                borderRight: "1px groove #e3e3e3",
              }}
            >
              {/* HEADER */}
              <div
                className=""
                style={{
                  borderBottom: "1px groove #e3e3e3",
                  padding: 15,
                  display: "flex",
                  gap: 30,
                  justifyContent: "space-between",
                }}
              >
                <h3>
                  <div
                    className="d-flex"
                    style={{ gap: 10, alignItems: "baseline" }}
                  >
                    <span className="h4" style={{ color: "#0059ff" }}>
                      {advertData?.user?.profile?.pname}
                    </span>
                    <span>
                      {advertData?.user?.verified ? (
                        <i
                          className="fas fa-check-circle"
                          style={{ color: "#85c4f9" }}
                          aria-hidden="true"
                        ></i>
                      ) : null}
                    </span>
                  </div>

                  <div className="d-flex" style={{ gap: 10 }}>
                    <Badge
                      variant={
                        String(advertData?.type)?.toLowerCase() == "buy"
                          ? "success"
                          : "danger"
                      }
                    >
                      {advertData?.type}
                    </Badge>
                    <span>
                      {(
                        Number(
                          advertData?.total_completed_orders /
                            advertData?.total_orders
                        ).toPrecision(2) * 100
                      ).toFixed(2)}
                      %{" "}
                      <small className="text-muted">
                        {t("completion rate")}
                      </small>
                    </span>
                  </div>
                </h3>

                <ul>
                  <li className="d-flex align-items-center">
                    <small
                      className="truncate text-muted"
                      title={`Advert ID: ${advertData?.id} `}
                      style={{ maxWidth: 200 }}
                    >
                      {advertData?.id}
                    </small>{" "}
                    <button
                      type="button"
                      className="btn"
                      style={{ fontSize: 12 }}
                    >
                      {t("Copy")}
                    </button>
                  </li>
                </ul>
              </div>

              {/* DESCRIPTION */}
              <div className="description" style={{ padding: 15 }}>
                <ul
                  style={{
                    display: "flex",
                    rowGap: 10,
                    flexWrap: "wrap",
                    justifyContent: "space-between",
                  }}
                >
                  {/* TOTAL ORDERS */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">{t("Total orders")}</small>
                    <span className="d-block">{advertData?.total_orders}</span>
                  </li>
                  {/* TOTAL COMPLETED */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">
                      {t("Completed orders")}
                    </small>
                    <span className="d-block">
                      {advertData?.total_completed_orders}
                    </span>
                  </li>
                  {/* PRICE */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">{t("Price")}</small>
                    <span className="d-block">
                      {advertData?.price}{" "}
                      <small className="text-muted text-uppercase">
                        {advertData?.fiat}
                      </small>
                    </span>
                  </li>
                  {/* AVAILABLE */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">{t("Available")}</small>
                    <span className="d-block">
                      {advertData?.available_qty}{" "}
                      <small className="text-muted text-uppercase">
                        {advertData?.crypto}
                      </small>
                    </span>
                  </li>
                  {/* TIME LIMIT */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">{t("Time limit")}</small>
                    <span className="d-block">
                      {advertData?.payment_ttl_mins}{" "}
                      <small className="text-muted">minute(s)</small>
                    </span>
                  </li>
                  {/* ORDER LIMIT */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">{t("Order limit")}</small>
                    <span className="d-block">
                      {advertData?.min_order_qty} - {advertData?.max_order_qty}{" "}
                      {/* <small className="text-muted">{advertData?.fiat}</sma> */}
                    </span>
                  </li>
                  {/* PAYMENT METHODS */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">
                      {t("Payment methods")}
                    </small>
                    <ul className="d-flex" style={{ gap: 10 }}>
                      {advertData?.payment_methods &&
                        Object.entries(advertData?.payment_methods)?.map(
                          ([key, value]) => {
                            return (
                              <li className="text-capitalize" key={key}>
                                <span>{String(value)}</span>
                              </li>
                            );
                          }
                        )}
                    </ul>
                  </li>
                  {/* REFRESH TIME */}
                  <li
                    className=""
                    style={{
                      flex: "1",
                      minWidth: "50%",
                      maxWidth: "45%",
                    }}
                  >
                    <small className="descr-title">{t("Remaining time")}</small>
                    <span className="d-block">
                      {timer}
                      <small className="text-muted">s</small>
                    </span>
                  </li>
                </ul>
              </div>

              {/* MISCELLENEOUS */}
              <hr />
              <div style={{ paddingLeft: 15, fontSize: 12, paddingRight: 15 }}>
                <Alert variant="warning">
                  <strong className="font-weight-bold">
                    TIPS &amp; GUIDES
                  </strong>
                  <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Blanditiis fuga adipisci delectus fugit quam enim,
                    praesentium consequatur provident dolore ullam totam officia
                    architecto obcaecati deleniti quas ea atque unde quae.
                  </p>
                </Alert>
              </div>
            </div>

            {/* FORM */}
            <div
              style={{
                flex: "1",
                display: "flex",
                flexDirection: "column",
                minWidth: 300,
              }}
            >
              <Form
                onSubmit={onSubmit}
                style={{
                  height: "100%",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                <div
                  style={{
                    padding: 15,
                    display: "flex",
                    flexDirection: "column",
                    gap: 10,
                  }}
                >
                  {/* CRYPTO QTY */}
                  <div
                    style={{
                      order:
                        String(advertData?.type)?.toLowerCase() == "buy"
                          ? 3
                          : 1,
                      display: "flex",
                      gap: 10,
                      flexDirection: "column",
                    }}
                  >
                    <FieldGroup>
                      <input
                        required
                        className="field__input"
                        placeholder={`Quantity`}
                        type="number"
                        min={advertData?.min_order_qty}
                        max={advertData?.max_order_qty}
                        step={Math.floor(advertData?.min_order_qty / 2)}
                        onChange={onQtyChange}
                        defaultValue={qty}
                      />
                      <span className="field__addon">
                        {String(advertData?.crypto)?.toUpperCase()}
                      </span>
                    </FieldGroup>
                    <small className="text-danger">
                      {errors && errors?.qty}
                    </small>
                  </div>
                  {/* IMAGE */}
                  <div
                    style={{ order: 2 }}
                    className="d-flex justify-content-center mb-4"
                  >
                    <Image
                      src={conversion}
                      style={{ width: 25 }}
                      alt="conversion"
                    />
                  </div>

                  {/* CRYPTO QTY */}
                  <FieldGroup
                    style={{
                      order:
                        String(advertData?.type)?.toLowerCase() == "buy"
                          ? 1
                          : 3,
                    }}
                  >
                    <div className="field__input truncate">
                      {total || (
                        <span className="text-muted">{`Amount in ${advertData?.fiat} will be calculated automatically`}</span>
                      )}
                    </div>
                    <span className="field__addon">
                      {String(advertData?.fiat)?.toUpperCase()}
                    </span>
                  </FieldGroup>
                </div>

                <OfferMisc>
                  {/* TRADE CONDITION */}
                  {advertData?.trade_conditions && (
                    <div className="misc">
                      <header className="header">
                        <h4 className="title">Trade conditions</h4>
                      </header>
                      <div className="description">
                        {advertData?.trade_conditions}
                      </div>
                    </div>
                  )}
                  {/* REMARK */}
                  {advertData?.remarks && (
                    <div className="misc">
                      <header className="header">
                        <h4 className="title">Remarks</h4>
                      </header>
                      <div className="description">{advertData?.remarks}</div>
                    </div>
                  )}
                </OfferMisc>
                <div
                  className="d-flex"
                  style={{
                    marginTop: "auto",
                    padding: 15,
                    display: "flex",
                    gap: 20,
                  }}
                >
                  <Button
                    type="submit"
                    disabled={
                      !errors || Object.keys(errors)?.length || isSubmitting
                    }
                    style={{ flex: "auto" }}
                  >
                    {isSubmitting ? t("Placing Order") : t(`Place order`)} (
                    {advertData?.type})
                  </Button>
                  <Button onClick={onClose}>{t("Cancel")}</Button>
                </div>
              </Form>
            </div>
          </div>
        ) : null}
      </Modal.Body>
    </Modal>
  );
}
